  color: black;
  margin: 4rem 0 0 2rem;
  padding: 0 ;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  -webkit-animation:;
          animation:  
    /* typing 10s steps(45, end), */
    /* blink .6s step-end infinite; */

  
    
    

}
@-webkit-keyframes typing{
  from {width: 0; border-right: 0.05em solid black;}
  to {width: 100%; border-right: 0.05em solid black;}
  
}
@keyframes typing{
  from {width: 0; border-right: 0.05em solid black;}
  to {width: 100%; border-right: 0.05em solid black;}
  
}
@-webkit-keyframes blink{
  from {border-color: transparent}
  50% {border-color: black}
}
@keyframes blink{
  from {border-color: transparent}
  50% {border-color: black}
}
