.nav{
  margin: 6rem 0 0 0rem;
	display: grid;
  justify-content: space-evenly;
  
}
.nav li{
	width: 9.7rem;
  padding: 2rem 1rem;
  margin: 1rem 0 0 0;
  list-style-type: none;
  border-radius: 999px;
  border: 2px solid grey;
  background-color: white;
  text-align: center;
  width: 20rem;
  cursor: pointer;

}
.nav li:hover{
  background-color: #e5a1a7;
}
.nav li:hover .navLink{
  color: white;
}

.navLink{
  font-size: 1.7rem;
	text-decoration: none;
	cursor:pointer;
	color: #e5a1a7;
	font-weight: 900;

}
pre{
  position: relative;
  margin: 3rem 2rem 0 2rem;
  color: grey;
