  height: 65px;
  display: flex;
  justify-content: flex-end;
  padding: 0 4rem 0 0;
  position: fixed;
  bottom: 0;
  -webkit-animation: none;
          animation: none;
  border-left: 40.8rem solid black;
  
}
span{
