}


/* /////////WORK///////// */
.work_container{
  background-color: black;
  display: grid;
  grid-row-gap: 5rem;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  justify-items: center;
  padding: 4rem 0 30rem 0;
}
.work_box{
  -webkit-perspective: 1000px;
          perspective: 1000px;
  background-color: transparent;
  width: 250px;
  height: 300px;
  border: 1px solid black;
  cursor: pointer;

}
.inner_box{
  width: 100%;
  height: 100%;
  position: relative;
  transition:  -webkit-transform 0.6s;
  transition:  transform 0.6s;
  transition:  transform 0.6s, -webkit-transform 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
}
.work_box:hover .inner_box{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.front_card{
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  }
img{
  width: 16rem;
  height: 19rem;
}
.back_card{
  width: 100%;
  height: 100%;
  /* border: 1px solid white; */
  /* border-radius: 10%; */
  background-color: white;
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding: 1rem 2rem;
  margin-bottom: -2rem;
}
.back_card h1{
  color: grey;
  text-align: center;
  text-decoration: underline;
  margin-bottom: 2rem;
  line-height: 1.4;
}
.back_card p{
  background-color:#c7646d ;
  color: white;
  font-size: 1.3rem;
  text-align: center;
  padding: 1.5rem 0.3rem;
  line-height: 1.2;
}
.visit{
  margin-top: 4rem;
  font-size: 1.5rem;
  text-align: center;
  height: 2rem;
}
.a{
  color:#e5a1a7;
  text-decoration: none;
  cursor: pointer;
}
.a:hover{
  color:#c7646d ;
}

/* //////ABOUT/////// */
.about h1{
	font-family: 'Inconsolata', monospace;
	font-size: 3rem;
	margin: 3rem 0 1rem 3rem;
  font-weight: 900;
  -webkit-animation: FadeIn ease 8s;
          animation: FadeIn ease 8s;
  /* animation-delay: 5s; */
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
 
}
.about_container{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	-webkit-animation: FadeIn ease 8s ;
	        animation: FadeIn ease 8s ;
  /* animation-delay: 5s; */
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
 position: relative;
 margin-top: 2rem;
}

img{margin-top: 2rem;}
.about_container p{
	background-color: rgba(250,250,250,0.5);
	color: black;
	/* width: 50%; */
	font-size: 1.5rem;
	text-align: left;
	padding: 1rem 3rem ;
	/* padding-top: 2rem; */
	font-family: 'Inconsolata', monospace;
	line-height: 2;
  position: relative;
}
.polaroid_box{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 1.5rem;
  justify-content: center;
  justify-items: center;
  position: relative;
}
.polaroid div {
  background: #ffffff;
  display: inline-block;
  padding: 1rem 1rem 2rem;
  margin-top: 2rem;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, .3);
}
.polaroid div:after {
  color: #333;
  font-size: 2rem;
  content: attr(title);
  position: relative;
  top: 1rem;
  font-family: 'Indie Flower', cursive;
  font-weight: bolder;
}
.polaroid img {display: block;}
